
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  background: #1885B4;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.board {
  min-width: 300px;
  min-height: 400px;
  border: 3px solid teal;
  background: #D8D8D8;
  padding: 20px 10px;
  border-radius: 22px;
  margin: 10px; 
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.board__title {
  font-size: 1.5rem;
  font-weight: 500;
  font-style: italic;
}

.items {
  width: 100%;
  border: 2px solid teal;
  padding: 10px;
  border-radius: 5px;
  margin: 5px 0;
  cursor: grab;
  background-color: azure;
  background: white;
}