.listitem{
    width: 238px;
    height: 40px;
    background: #FAFAFA;
    box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.1);
    border-radius: 3px;
}
.btn{
    background-color: white;
    width: 100%;
    border-radius: 3px;
    color: #585858;
    font-family: 'Helvetica Neue';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    border: 1px solid teal;
    position: relative;
    z-index: 1;
}
.btn:hover{
    background: #D8D8D8;
}
.btn-2{
    background-color: white;
    position: absolute;
    cursor: move;
    width: 100%;
    border: 2px solid teal;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: space-around;
}
.delete{
    background-color: whitesmoke;
    width: 50px;
    border: 1px solid teal;
    border-radius: 3px;
    color: #585858;
}
.delete:hover{
    background: #D8D8D8;
}


