.list{
    width: 250px;
    border: 2px solid teal;
    height: 50px;
    background: #EBECF0;
    border-radius: 6px;
    margin: 10px ;
    display: inline-block;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: relative;
    gap: 10px;
}
.list__title{
    font-family: 'Helvetica Neue';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
}
.list__menu{
    width: 20px;
    height: 20px;
    position: absolute;
    top: -8px;
    right: 15px; 
    color: #909090;  
}

